<template>
    <p-card style="width: 80%; margin: auto">
        <template #content>
            <div class="p-grid">
                <div class="p-col">
                    <p-button
                        label="Scan Barcode"
                        class="p-button-lg p-m-2"
                        style="width: 100%;height: 50px"
                        @click="goToScanPage"
                    />
                </div>
                <div class="p-col">
                    <p-button
                        label="Patient Barcodes"
                        class="p-button-lg p-m-2"
                        style="width: 100%;height: 50px"
                        @click="goToPatientBarcodes"
                    />
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { useRouter } from "vue-router";
import { useStore } from "vuex";
export default {
    setup() {
        const router = useRouter();
        const store = useStore();
        return {
            goToScanPage: () => {
                router.push({ path: "/barcodes/scan" });
            },
            goToPatientBarcodes: () => {
                router.push({ path: "/barcodes/patient-barcodes" });
            }
        };
    }
};
</script>
